@import url(https://fonts.googleapis.com/css2?family=Signika:wght@300;400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes slideAnimation-out{0%{transform:translateX(0%)}100%{transform:translateX(-100%)}}@keyframes slideAnimation-out{0%{transform:translateX(0%)}100%{transform:translateX(-100%)}}@-webkit-keyframes slideAnimation-in{0%{transform:translateX(100%)}100%{transform:none}}@keyframes slideAnimation-in{0%{transform:translateX(100%)}100%{transform:none}}

.root{position:absolute;bottom:0px;display:block;height:100%}.App{display:flex;flex-direction:column;position:absolute;width:100%;height:100%;top:0;bottom:0;overflow-y:auto;background-color:#323036;color:#DBD3D8}.bar{top:0;position:relative;display:flex;z-index:1;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;color:#323036;background-color:#DBD3D8;min-height:-webkit-fit-content;min-height:-moz-fit-content;min-height:fit-content}.bar .title{flex-grow:1;margin-top:auto;margin-bottom:auto;margin-right:auto;margin-left:25px;font-size:50px;font-family:"Signika",sans-serif}@media only screen and (max-width: 600px){.bar .title{font-size:25px}}.bar .information{margin-top:auto;margin-bottom:auto;margin-right:25px}.bar li{margin-bottom:3px}.Body{overflow-y:auto}.Body .section{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-left:2.5%;margin-right:2.5%;max-width:95%}.Body .section h1{font-size:50px;font-style:italic}.Body .section .info{display:flex;flex-direction:row;margin-right:25px}@media only screen and (max-width: 600px){.Body .section .info{flex-direction:column}}.Body .section .info p{width:50%}@media only screen and (max-width: 600px){.Body .section .info p{width:100%}}.Project .content{display:flex;flex-direction:column;align-items:center;justify-content:center}.Project .contentBox{display:flex;align-items:center;flex-direction:row;margin-bottom:5px}@media only screen and (max-width: 600px){.Project .contentBox{flex-direction:column}}@media only screen and (min-width: 601px){.Project .contentBox{flex-direction:row}.Project .contentBox p{width:500px}.Project .contentBox .oneLine{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin-left:5%;margin-right:0px}.Project .contentBox .column{border-right:0px;margin-right:5px}}.Project a{font-size:larger;color:#DBD3D8;transition:all .5s linear}.Project a:hover{color:#00FDDC;font-size:64}.Project li{margin-left:2px}.Project li:hover{transition:font-size 0.125s linear;color:#00FDDC;font-size:105%}.SlideShow{position:relative;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;max-height:500px;overflow:hidden}.SlideShow .traverse{width:40px;background-color:black;top:25px;bottom:25px;opacity:.5}.SlideShow .traverse:hover{opacity:1}.SlideShow .back{left:0px;position:absolute}.SlideShow .back::after{content:'';position:absolute;border-top:16px solid transparent;border-bottom:16px solid transparent;border-right:20px solid white;bottom:calc(50% - 16px);left:3px}.SlideShow .forward{position:absolute;right:0px}.SlideShow .forward::after{content:'';position:absolute;border-top:16px solid transparent;border-bottom:16px solid transparent;border-left:20px solid white;margin-left:auto;bottom:calc(50% - 20px);right:3px}@media only screen and (max-width: 1100px){.SlideShow .SlideShowImage{width:100%}}@media only screen and (min-width: 1101px){.SlideShow .SlideShowImage{max-height:500px}}.SlideShow .buttonBox{display:flex;flex-direction:row;align-items:center;justify-content:center;position:absolute;bottom:10px;left:0;right:0}.SlideShow .button{z-index:5;position:relative;width:25px;height:25px;margin-right:10px;border-radius:100%;opacity:.5;background-color:#6D6A75;transition:background-color 1s linear;transition:opacity 1s linear}.SlideShow .button:hover{background-color:#00FDDC}.description{margin-right:0px;max-width:500px}.Table{cursor:default;display:flex;flex-direction:row;justify-content:center;border-style:solid;border-width:4px;border-color:#6D6A75;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-top:25px}.Table .column{position:relative;display:flex;flex-direction:column;width:100%;border-style:solid;border-bottom-width:0px;border-left-width:0;border-top-width:0;border-color:#6D6A75}.Table .column li{margin-left:2px}.Table .column li:hover{transition:font-size 0.125s linear;color:#00FDDC;font-size:105%}.Table .columnTitle{font-family:"Source Sans Pro",sans-serif;border-bottom-width:4px;border-right-width:0px;border-left-width:0;border-top-width:0;border-style:solid;border-color:#6D6A75;padding-left:auto;padding-right:auto}.Contact{width:500px;margin-top:25px;display:flex;flex-direction:column;align-items:center;border-radius:25px;padding:10px;margin-bottom:15px;background-color:#DBD3D8;transition:transform 2s;overflow:hidden}@media only screen and (max-width: 600px){.Contact{width:300px}}.Contact .textBox{font-size:x-large;color:#323036;text-align:center;transition:transform 1s linear;transition-delay:1s;transform:translate(-1000px, 0px)}.Contact button{width:120px;height:60px;font-family:"Source Sans Pro",sans-serif;font-size:x-large;font-style:bold;background-color:#6D6A75;border-radius:15px;border-style:none;box-shadow:none;outline:none;transition:background-color 1s ease;transition:transform 1s linear;transition-delay:1s;transform:translate(1000px, 0px)}.Contact button:hover{background-color:#00FDDC}.Contact.disabled{visibility:hidden}.Contact.animated{visibility:visible;transform:rotateY(360deg);transform:rotateX(360deg)}.Footer{position:relative;bottom:auto}.Footer p{margin-left:25px}.Reflection{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-left:12.5%;margin-right:12.5%}.Reflection .info{font-size:120%}.Reflection .link{color:#ffff;font-size:150%}.Reflection .image{width:50%}

